import { StylesProvider } from '@material-ui/core/styles';
import type { ProviderProps } from 'providers/types';

/**
 * Определяет место, куда будут добавлены стили MUI внутри <head>
 */
export const withMuiStyles = (WrappedComponent: ProviderProps) => () => {
  return <StylesProvider injectFirst>{WrappedComponent()}</StylesProvider>;
};
