/**
 * react-redux не умеет диспатчить thunk из redux-toolkit.
 * Раньше это было в d.ts react-redux, потом оттуда убрали.
 * export function useDispatch<TDispatch = Dispatch<any>>(): TDispatch;
 * Дополнительно можно посмотреть вот здесь https://github.com/reduxjs/redux-thunk/issues/333
 * Теперь рекомендованный метод - https://react-redux.js.org/tutorials/typescript-quick-start#define-typed-hooks.
 * Дополнительным плюсом идёт то, что теперь не надо везде импортировать AppState.
 */
import { useDispatch, useSelector } from 'react-redux';
import type { TypedUseSelectorHook } from 'react-redux';
import type { AppState, AppDispatch } from './types';

export const useAppDispatch: () => AppDispatch = useDispatch;
export const useAppSelector: TypedUseSelectorHook<AppState> = useSelector;
